var isLoadInterFace = false;
var _self;
export default {
	data() {
		return {
			loadData:true,
			loginInfo:{},
			is_collect:0,
			shopId:0,
			crIndex:0,
			showShareBlock:false,
			isShowTip:false,
			shopInfo:{},
			couponList:[],
			currentPage:1,
			totalPage:1,
			isLast:true
		}
	},
	onLoad(opt) {
		_self=this;
		var loginRes = this.checkLogin('../index/index', '2');
		if(!loginRes){return false;}
		this.loginInfo = loginRes;
		
		//console.log(opt);
		if(opt.id != undefined){
			_self.shopId = opt.id;
			_self.searchShopInfo();
			_self.lookLikeStatus();
		}
	},
	onShow() {
		
	},
	onShareAppMessage() {
		return {
			title: "来和我一起Get新技能吧！",
			path: '/pages/index/index',
			imageUrl:'http://www.jfcma.com/public/images/jfcm/logo.png'
		}
	},
	methods: {
		//预览大图
		lookBigImg(imgdata){
			imgdata=[imgdata];
			uni.previewImage({
				urls: imgdata,
				current:1,
				success(e) {
					//console.log(e);
				},fail(e) {
					
				},complete(e) {
					
				}
				
			});
		},
		//拨打电话
		callPhone(){
			
			if(_self.checkPhone(_self.shopInfo.list.tel)){
				uni.makePhoneCall({
				    phoneNumber: _self.shopInfo.list.tel
				});
			}
			
		},
		//查询店铺是否收藏
		lookLikeStatus(){
			//console.log(_self.shopId);
			//console.log(_self.loginInfo.uid);
			uni.request({
			    url: _self.$httpUrl+'index.php/api/index/isCollect', 
				method:"POST",
				data:{
					storeid:_self.shopId,
					user_id:_self.loginInfo.uid
				},
			    success: (res) => {
			     
			      //console.log(res);
				  
				  if(res.data.status == 1){
					_self.is_collect = res.data.result;
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
		},
		//店铺收藏
		likeShop(x){
			uni.request({
			    url: _self.$httpUrl+'api/index/collect', 
				method:"POST",
				data:{
					storeid:_self.shopId,
					type:x,
					user_id:_self.loginInfo.uid
				},
			    success: (res) => {
			     
			      //console.log(res);
				  
				  if(res.data.status == 1){
					_self.is_collect = x;
					if(x == 0){
						uni.showToast({
							title:"取消收藏",
							icon:"none"
						})
					}else{
						uni.showToast({
							title:"收藏成功",
							icon:"none"
						})
					}
				  }
				  
			    },fail: (e) => {
					
			    },complete: (e) => {
					
			    }
			});
		},
		//加载下一页
		nextPage(){
			if(_self.isLast){
				uni.showToast({
					title: '没有更多了',
					icon:"none"
				});
			}else{
				if(!isLoadInterFace){
					isLoadInterFace = true;
					var pageNum = _self.currentPage + 1;
					_self.getShopCoupon(pageNum);
				}
			}
		},
		//查询店铺优惠券列表
		getShopCoupon(pageNum){
			uni.request({
			    url: _self.$httpUrl+'index.php/api/index/storeCoupon', 
				method:"POST",
				data:{
					storeid:_self.shopId,
					page:pageNum,
					limit:3
				},
			    success: (res) => {
			      //console.log(res);
				  if(res.data.status == 1){
					  _self.couponList = _self.couponList.concat(res.data.result.data);
					  _self.currentPage = res.data.result.current_page;
					  _self.isLast = _self.currentPage == res.data.result.last_page ? true : false;
				  }
			     
			    },fail: (e) => {
					
			    },complete: (e) => {
					isLoadInterFace = false;
			    }
			});
		},
		//查询店铺详情
		searchShopInfo(){
			
			uni.request({
			    url: _self.$httpUrl+'api/index/storeDetail', 
				method:"POST",
				data:{
					id:_self.shopId
				},
			    success: (res) => {
			      //console.log(res);
				  if(res.data.status == 1){
					  _self.shopInfo = res.data.result;
					  _self.getShopCoupon(1);
				  }
			     
			    },fail: (e) => {
					
			    },complete: (e) => {
					_self.loadData = false;
			    }
			});
			
		},
		showTipsFun(){
			_self.isShowTip = true;
			setTimeout(function(){
				_self.isShowTip = false;
			},1500);
		},
		//分享好友
		shareFriendFun(){
			uni.share({
				provider: "weixin",
				scene: "WXSceneSession",
				type: 0,
				href: "www.baidu.com",
				title: "来和我一起Get新技能吧！",
				summary: "我用这款App领取各种吃喝玩乐优惠券，还拿到了不少的分润哦，快来和我一起享优惠、拿分润吧！",
				imageUrl: "http://www.jfcma.com/public/images/jfcm/logo.png",
				success: function (res) {
					//console.log("success:" + JSON.stringify(res));
					_self.tipMsg("感谢分享！")
				},
				fail: function (err) {
					//console.log("fail:" + JSON.stringify(err));
				}
			});
		},
		//分享朋友圈
		shareCircleFun(){
			
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 0,
			    href: "www.baidu.com",
			    title: "来和我一起Get新技能吧！",
			    summary: "我用这款App领取各种吃喝玩乐优惠券，还拿到了不少的分润哦，快来和我一起享优惠、拿分润吧！",
			    imageUrl: "http://www.jfcma.com/public/images/jfcm/logo.png",
			    success: function (res) {
			        console.log("success:" + JSON.stringify(res));
					_self.tipMsg("感谢分享！")
			    },
			    fail: function (err) {
			        console.log("fail:" + JSON.stringify(err));
			    }
			});
			
		},
		shareBlockFun(){
			_self.showShareBlock = !_self.showShareBlock;
		},
		swiperChange(e){
			_self.crIndex = e.detail.current;
		},
		backBeforePage: ()=>{
			
			uni.navigateBack({
				delta:1
			})
		},
		getTicketFun(e){
			
			console.log(e);
			var cid = e.currentTarget.dataset.id;
			uni.request({
				url: _self.$httpUrl+'api/index/getCoupon', 
				method:"POST",
				data:{
					id:cid,
					user_id:_self.loginInfo.uid
				},
				success: (res) => {
				  console.log(res);
				  if(res.data.status == 1){
					  if(!_self.isShowTip){
					  	_self.showTipsFun();
					  }
				  }else{
					  uni.showToast({
					  	title:res.data.msg,
						icon:"none"
					  })
				  }
				 
				},fail: (e) => {
					
				},complete: (e) => {
					
				}
			});
		}
		
	}
}

